	margin-bottom: 5px;
}

div#azan-top > span {
	margin-left: 10px;
} */

div#weather {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	justify-content: flex-end;
	align-self: flex-start;
	margin-top: -20px;
}

div#weather > img {
	width: 70px;
}

p.weather-city {
	width: 100%;
	text-align: right;
	margin: 0;
	margin-top: -10px;
}

p.weather-temp {
	margin: 0;
	font-size: 1.7rem;
	margin-left: -10px;
}

a:link,
a:visited {
	background-color: #f44336;
	color: white;
	padding: 14px 25px;
	border-radius: 50px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
}

a:hover,
a:active {
	background-color: red;
}
